.containers{
    position:fixed;
    bottom:30px;
    right:30px;
}

.popupContainer{
    box-shadow: 0 0 18px 8px rgba(0,0,0,0.1);
    width:400px;
    right:-15px;
    bottom: 95px;
    max-height: 550px;
    position: absolute;
    transition: max-height .2s ease;
    font-family: Helvetica, Arial, sans-serif;
}

.popupContainer .hidden{
    max-height: 0px;
}

.popupContainer h2{
    margin:0;
    padding:20px;
    color: #fff;
    background-color: #2356c9;
    display: flex;
}

.popupContainer h2 > img{
    height: 30px;
    width: 30px;
    padding-right: 10px;
    border-radius: 5px;
}

.popupContainer .contents{
    margin: 20px 10px;
    border: 1px solid #dbdbdb;
    padding: 10px;
    display:flex;
    background-color: #fff;
    flex-direction: column;
    text-align: center;
    border-radius: 20px;
}

.popupContainer p {
    color: #777;
}

.popupContainer .boxUpload{
    display:grid;
    place-items: center;
    border: 1px dashed #799CD9;
    border-radius: 20px;
    background: #FBFBFF;
}

.popupContainer .boxUpload > p{
    color: #444;
}

.boxUpload .imageUpload{
    display:flex;
    flex-wrap: wrap;
    cursor: pointer;
}

.boxUpload .imageUpload img{
    height: 100px;
    width: 100px;
}

.boxUpload .imageUpload label{
    cursor: pointer;
}

.boxUpload  .imageUpload > input{
    display:none;
}

.boxUpload .imageUpload label:hover{
    opacity: 0.8;
}

.imgPreview #uploadedImg{
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 20px;
}

.imgPreview{
    position: relative;
}

.close-icon{
    background-color: #000;
    position:absolute;
    z-index: 10;
    right:15px;
    top: 10px;
    cursor:pointer;
    height:24px !important;
    width:24px !important;
    border-radius: 15px;
    opacity: 0.7 !important;
}

.close-icon:hover{
    opacity: 1 !important;
}

.popupContainer form *{
    margin:5px 0;
}

.popupContainer form input{
    padding:10px;
}

.popupContainer form button{
    cursor:pointer;
    background-color:#2356c9;
    color: #fff;
    border:0;
    border-radius:4px;
    padding: 10px;
}

.popupContainer form button:hover{
    background-color: #16632f;
}

.icons{
    cursor:pointer;
    top:9px;
    left:9px;
    width:70%;
    position:absolute;
    transition: transform .3s ease;
}

.hiddens{
    transform: scale(0);
}

.btnContainer{
    background-color: black;
    width: 60px;
    height: 60px;
    border-radius: 50%;
}

.progressBar{
    padding: 0 !important;
    margin: 0 !important;
}

.progressBar .labelScoreContainer{
    height: 0px;
    transform: scale(0);
}